.station-container {
    display: flex;
    flex-direction: column;
    background-color: #f4f4f4;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
  }